import React from "react"

import "../styles/components/CardHeader.scss"

import facebook from "../styles/img/facebook.svg"
import instagram from "../styles/img/instagram.svg"

const CardHeader = () => {
  return (
    <div className={"card_header"}>
      <i className={"card_header_decor card_header_decor_top"} />
      <h1 className={"card_header_title"}>
        <span>Lady</span>
        <span>school</span>
      </h1>
      <p>женский онлай курс</p>
      <div className={"divider_title"} />
      <a href={"/"}>Смотреть курс</a>
      <i className={"card_header_decor card_header_decor_bottom"} />

      <nav className={"card_header_nav"} role="navigation">
        <div className="card_header_nav_toggle">
          <input type="checkbox" />
          <span />
          <span />
          <span />
          <div className="card_header_nav_menu">
            <ul>
              <li><a href={"#about"}>О курсе</a></li>
              <li><a href={"#program"}>Программа</a></li>
              <li><a href={"#price"}>Стоимость</a></li>
              <li><a href={"#start"}>Инструкция</a></li>
              <li><a href={"#footer"}>Контакты</a></li>
              <li className={'button'}>
                <button className={'btn-sm btn-sm_login '}>Выйти из аккаунта</button>
              </li>
            </ul>
            <span className={"divider divider_horizontal"} />
            <p>Узнайте о нас больше</p>
            <div className={"card_header_nav_menu_social"}>
              <a href={"/"} target={"_blank"} rel={"noreferrer"}><img src={facebook} alt={"facebook"} /></a>
              <a href={"/"} target={"_blank"} rel={"noreferrer"}><img src={instagram} alt={"instagram"} /></a>
            </div>
          </div>
        </div>
      </nav>
    </div>
  )
}

export default CardHeader
