import React, { useEffect, useRef, useState } from "react"

import Form from "react-bootstrap/Form"

import useDebounce from "../Debounce"

import Modal from './Modal'
import ConfirmedModal from './ConfirmedModal'

import { identityValidationPasswords, validationEmail, validationName } from "../../lib/validationForm"

const RegistrationModal = ({ isShow, handleCloseModal}) => {
  const [dataUser, setDataUser] = useState({
    name: '',
    email: '',
    password: '',
    repeatPassword: '',
    license: false
  })
  const [errorMsg, setErrorMsg] = useState({
    name: '',
    email: '',
    password: '',
    repeatPassword: ''
  })

  // const emailInput  = useRef()
  //
  // const focusTextInput = () => {
  //   console.log(emailInput)
  //   emailInput.current.focus()
  // };

  const [openConfirmedModal, setOpenConfirmedModal] = useState(false)

  const handleCloseConfirmedModal = () => {
    setOpenConfirmedModal(false)
  }

  const onHandleSubmitForm = (event) => {
    event.preventDefault()

    const isNoSubmit = Object.values(dataUser).some(param => !param) || dataUser.password !== dataUser.repeatPassword

    if (isNoSubmit) return

    setOpenConfirmedModal(true)

    console.log(dataUser)
  }

  const handleData = (event, query) => {
    setDataUser({
      ...dataUser,
      [query] : event.target.value
    })
  }

  const debouncedCheckedName = useDebounce(dataUser.name, 1000)
  const debouncedCheckedEmail = useDebounce(dataUser.email, 1500)
  const debounceCheckedPassword = useDebounce(dataUser.password, 1000)
  const debounceCheckedRepeatPassword = useDebounce(dataUser.repeatPassword, 1000)

  useEffect(() => {
    const reqValidNameError = validationName(dataUser.name)

    setErrorMsg({
      ...errorMsg,
      name: reqValidNameError,
    })
  }, [debouncedCheckedName])

  useEffect(() => {
    const reqValidEmailError = validationEmail(dataUser.email)
    setErrorMsg({
      ...errorMsg,
      email: reqValidEmailError,
    })
  }, [debouncedCheckedEmail])

  useEffect(() => {
    const reqValidPassError = identityValidationPasswords(dataUser.password, dataUser.repeatPassword)
    setErrorMsg({
      ...errorMsg,
      ...reqValidPassError,
    })
  }, [debounceCheckedPassword, debounceCheckedRepeatPassword])

  const disabledButton = Object.values(dataUser).some(param => !param) || Object.values(errorMsg).some(param => param)

  return (
    <Modal isShow={isShow} handleCloseModal={handleCloseModal}>
      <h3>Стоимость курса: <span>399грн</span></h3>
      <Form className={""} name={"registration"} onSubmit={onHandleSubmitForm}>
        <Form.Group controlId="name" className={""}>
          <Form.Label>Как вас зовут?</Form.Label>
          <Form.Control
            type="text"
            placeholder="Ваше имя"
            autoComplete="off"
            value={dataUser.name}
            onChange={(e) => handleData(e, "name")}
            className={errorMsg.name ? "form-control-error" : ""}
          />
          {errorMsg.name && (
            <span className='form-error-text'>{errorMsg.name}</span>
          )}
        </Form.Group>

        <Form.Group controlId="email" className={""}>
          <Form.Label>Введите Email, на который придёт доступ</Form.Label>
          <Form.Control
            type="text"
            placeholder="Введите Email"
            autoComplete="off"
            value={dataUser.email}
            onChange={(e) => handleData(e, "email")}
            className={errorMsg.email ? "form-control-error" : ""}
          />
          {errorMsg.email && (
            <span className='form-error-text'>{errorMsg.email}</span>
          )}
        </Form.Group>

        <Form.Group controlId="password" className={""}>
          <Form.Label>Придумайте пароль для доступа к курсу</Form.Label>
          <Form.Control
            type="text"
            placeholder="Введите пароль"
            autoComplete="off"
            value={dataUser.password}
            onChange={(e) => handleData(e, "password")}
            className={errorMsg.password ? "form-control-error" : ""}
          />
          {errorMsg.password && (
            <span className='form-error-text'>{errorMsg.password}</span>
          )}
        </Form.Group>

        <Form.Group controlId="repeatPassword" className={""}>
          <Form.Label>Проверим совпадают ли пароли</Form.Label>
          <Form.Control
            type="text"
            placeholder="Введите пароль еще раз"
            autoComplete="off"
            value={dataUser.repeatPassword}
            onChange={(e) => handleData(e, "repeatPassword")}
            className={errorMsg.repeatPassword ? "form-control-error" : ""}
          />
          {errorMsg.repeatPassword && (
            <span className='form-error-text'>{errorMsg.repeatPassword}</span>
          )}
        </Form.Group>

        <div className='custom_modal_license'>
          <input type="checkbox" checked={dataUser.license} onClick={(event) => setDataUser({ ...dataUser, license: !dataUser.license })} />
          <a href={"/"}>Соглашаюсь с <span>Политикой конфиденциальности</span></a>
        </div>
        {disabledButton ? (
          <button disabled={true} className='btn disabled-btn'>Приобрести курс</button>
        ) : (
          <button className='btn'>Приобрести курс</button>
        )}
        <p>Доступ к курсу на 6 месяцев</p>
      </Form>
      <ConfirmedModal
        isShow={openConfirmedModal}
        handleCloseConfirmedModal={handleCloseConfirmedModal}
        email={dataUser.email}
      />
    </Modal>
  )
}

export default RegistrationModal