import React from "react"

import "../styles/components/CardAboutUs.scss"
import VideoComponent from "./VideoComponent"

const CardAboutUs = () => {
  return (
    <div className={"card_about_us"}>
      <h2>О курсе</h2>
      <p>Этот курс - совокупность знаний, которые помогут вам расскрыть вашу внутреннюю и внешнюю красоту. Вы
        примите себя, почувствуете значимой и достойной, поймёте к чему в жизни стремиться и как этого достичь</p>
      <VideoComponent>
        <iframe
          src="https://www.youtube.com/embed/iDnhX2E7XuY"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
          title={"video"}
        />
      </VideoComponent>
    </div>
  )
}

export default CardAboutUs