import React from "react"

import arrow from "../styles/img/arrow.svg"
import email from "../styles/img/e-mail.svg"
import payment from "../styles/img/payment.svg"
import done from "../styles/img/done.svg"

import "../styles/components/CardStartCourse.scss"

const CardStartCourse = ({ setIsShow }) => {
    return (
      <div className={"card_start"}>
        <h2>Как начать курс</h2>
        <img src={payment} alt={"payment"} className={"img img_payment"} />
        <span className={"card_start_step"}>Оплатите курс</span>
        <p>После оплаты на указанную вами почту придёт сообщение: “Оплата курса прошла успешно” (Проверьте папку
          “Спам”)</p>
        <img src={arrow} alt={"arrow down"} className={"img_arrow"} />
        <img src={email} alt={"e-mail"} className={"img img_email"} />
        <span className={"card_start_step"}>Откройте письмо</span>
        <p>Перейдите в письме по ссылке на страницу с уроками</p>
        <img src={arrow} alt={"arrow down"} className={"img_arrow"} />
        <img src={done} alt={"done"} className={"img img_done"} />
        <span className={"card_start_step"}>Готово!</span>
        <p>Приятного просмотра :)</p>
        <button className={"btn"} onClick={() => setIsShow(true)}>Приобрести курс</button>
      </div>
    )
}

export default CardStartCourse