import React from "react"

import "../styles/components/CardSummary.scss"

const CardSummary = () => {
    return (
        <div className={'card_summary'}>
            <h2>Результат</h2>
          <p>
            В течении курса вы получите знания и опыт профессионалов своего дела с помощью которых вы сможете
            осуществлять свои мечты и быть неотразимой!
          </p>
          <span className={'divider divider_horizontal'}/>
          <p>Вы расскроете вашу женственность, внутреннюю и внешнюю красоту, полюбите своё отражение, поверите в себя и начнёте двигаться к своей цели</p>
        </div>
    )
}

export default CardSummary