import React, { useState } from "react"
import './styles/App.scss';

import Container from "react-bootstrap/Container"
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

import CardHeader from './components/CardHeader'
import CardMission from './components/CardMission'
import CardAboutUs from "./components/CardAboutUs"
import CardNeeds from "./components/CardNeeds"
import CardSummary from "./components/CardSummary"
import CardProgram from "./components/CardProgram"
import CardPrice from "./components/CardPrice"
import CardStartCourse from "./components/CardStartCourse"
import Footer from "./components/Footer"
import RegistrationModal from "./components/modals/RegistrationModal"


function App() {
    const [isShow, setIsShow] = useState(false)

    const handleCloseModal = () => {
        setIsShow(false)
    }

  return (
      <>
          <Container className={'container'}>
              <Row className={'m-0'}>
                  <Col sm={12} className={'card'}>
                      <CardHeader/>
                  </Col>
                  <Col sm={12} className={'card card_default'} id={'about'}>
                      <CardAboutUs/>
                  </Col>
                  <Col sm={12} className={'card card_default'}>
                      <CardMission/>
                  </Col>
                  <Col sm={12} className={'card card_default'} id={'program'}>
                      <CardProgram/>
                  </Col>
                  <Col sm={12} className={'card card_default'}>
                      <CardSummary/>
                  </Col>
                  <Col sm={12} className={'card card_default'} id={'price'}>
                      <CardPrice setIsShow={setIsShow}/>
                  </Col>
                  <Col sm={12} className={'card card_default'} id={'start'}>
                      <CardStartCourse setIsShow={setIsShow}/>
                  </Col>
                  <Col sm={12} className={'card card_default'}>
                      <CardNeeds/>
                  </Col>
              </Row>
          </Container>
          <RegistrationModal isShow={isShow} handleCloseModal={handleCloseModal}/>
          <Footer id={'footer'}/>
      </>
  );
}

export default App;
