import React from "react"

import instagram from '../styles/img/instagram.svg'
import facebook from '../styles/img/facebook.svg'
import logo from '../styles/img/lady-school-logo.svg'

import '../styles/components/Footer.scss'

const Footer = () => {
    return (
        <div className={'footer'}>
            <h5>Узнайте о нас больше</h5>
            <div className={'footer_social'}>
                <a href={'/'} target={'__blank'}><img src={facebook} alt={'facebook'}/></a>
                <a href={'/'} target={'__blank'}><img src={instagram} alt={'instagram'}/></a>
            </div>
            <div className={'divider divider_horizontal'}/>
            <p>Если у вас возникли вопросы пишите нам:</p>
            <span className={'email'}>ladyschool@gmail.com</span>
            <div className={'divider divider_horizontal'}/>
            <ul>
                <li><a href={'/'}>Договор</a></li>
                <li><a href={'/'}>Соглашение</a></li>
                <li><a href={'/'}>Политика</a></li>
            </ul>
            <div className={'logo'}>
                <a href={'/'}><img src={logo} alt={'logo'}/></a>
                <span>Lady School 2020</span>
            </div>
        </div>
    )
}

export default Footer