import React from "react"

import Modal from "react-bootstrap/Modal"

import "../../styles/components/modals/ConfirmedModal.scss"

const ConfirmedModal = ({ isShow , handleCloseConfirmedModal, email}) => {

  return (
    <Modal show={isShow} centered dialogClassName={"confirmed_modal"}>
      <div className={"confirmed_modal_wrapper"}>
        <p>Вы правильно ввели свой Email?</p>
        <span>{email}</span>
        <div className='d-flex justify-content-between flex-wrap'>
          <button className='btn-outline' onClick={handleCloseConfirmedModal}>Редактировать</button>
          <button className='btn' onClick={handleCloseConfirmedModal}>Всё верно!</button>
        </div>
      </div>
    </Modal>
  )
}

export default ConfirmedModal