import React from "react"

import Modal from 'react-bootstrap/Modal'

import close from '../../styles/img/icons/close_light.svg'

import "../../styles/components/modals/Modal.scss"

const ModalWrapper = ({children, isShow, handleCloseModal}) => {

  return (
    <Modal show={isShow} onHide={handleCloseModal} centered dialogClassName={'custom_modal'}>
      <div className={'custom_modal_wrapper'}>
        <img src={close} role={'button'} tabIndex={0} alt={'close'} onClick={handleCloseModal} className={'custom_modal_close'}/>
        {children}
      </div>
    </Modal>
  )
}

export default ModalWrapper
