import React from "react"

import "../styles/components/CardNeeds.scss"

const CardNeeds = () => {
  return (
    <div className={"card_needs"}>
      <h2>Нужен ли мне этот курс?</h2>
      <p className={"card_needs_italic"}>
        “У меня ужасная фигура! У меня токсичные отношения! Меня не понимают и не любят родители! Я не люблю учиться!
        Почему у меня нет друзей?
        Я не люблю свою работу! Хочу инстаграм как у неё! Что делать дальше?...”
      </p>
      <div className={"divider divider_horizontal"} />
      <p>
        Знакомо?<br />У каждой из нас возникали подобные мысли...
      </p>
      <div className={"divider divider_horizontal"} />

      <p className={"card_needs_people"}>
        Мало кто говорит о коллосальной работе проделанной людьми, которые достигли того, что нас вдохновляет...
        Этот курс - ваша работа над собой, которая поможет достичь желаемого!
      </p>
    </div>
  )
}

export default CardNeeds